/// Import order:
/// 1.  Encoding
/// 2.  Variables
/// 3.  Foundation
/// 4.  Utilites
/// 5.  Flats CMS Styles

/// Encoding
/// --------
@charset 'UTF-8';

/// Variables
/// ---------
@import "variables/_animation.scss";
@import "variables/_colors.scss";
@import "variables/_foundation.scss";
@import "variables/_type.scss";

/// Foundation
/// ----------
/// Source: http://foundation.zurb.com/docs/
/// Note: We're only importing Foundation's function's and mixins, not
/// generating CSS.
@import 'foundation';

/// Utilities
/// ---------
@import "utilities/_mixins.scss";

/// Flats CMS Styles
/// ----------------
@import 'cms-styles/flats-only/d8/d8';
